import React, { Fragment } from "react";
import { ImSad } from "react-icons/im";
import {
  MdCheckBox,
  MdCheckBoxOutlineBlank,
  MdCheckCircle,
  MdKeyboardBackspace,
  MdOutlineArrowForward,
  MdSearch,
} from "react-icons/md";

import { connect } from "react-redux";
import {
  Auth,
  Basic,
  FC_GetFreeVacanciesByDistrictAndUser,
  FC_GetTransfersReasons,
  FC_SendTransferRequest,
  GetFreeVacanciesByDistrictAndUser_Interface,
  Placement,
  ReasonsTransfers,
  SendTransferRequestInterface,
  FC_UpdateUserDocuments,
  FT_UpdateUserDocuments,
  FC_GetVacantByDistrictAndPost,
  GetVacantByDistrictAndPost_Interface,
} from "../../actions";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Heading from "../../components/Heading/Heading";
import TextArea from "../../components/Inputs/TextArea";
import TextInput from "../../components/Inputs/TextInput";
import Loading from "../../components/Loading/Loading";
import UserDocuments from "../../components/UserDocuments/UserDocuments"; // UserDocumentsInterface,
import { StoreState } from "../../reducers";
import { search } from "../../utils/functions";
import CardTransferRequest from "./CardTransferRequest";
import TransferClosed from "./TransferClosed";

// props for the component
interface AppProps {
  auth: Auth;
  basic: Basic;
  placement: Placement;
  onClick: Function;
  reloadTransfers: () => void;
  FC_UpdateUserDocuments: FT_UpdateUserDocuments;
  close: () => void;
}

interface AppState {
  loading_district: boolean;
  error: {
    target:
      | "main-success"
      | "main-error"
      | "error"
      | "transfer-error"
      | "transfer-success"
      | null;
    msg: string;
  };
  districts: GetVacantByDistrictAndPost_Interface[];
  schools: GetFreeVacanciesByDistrictAndUser_Interface[];
  selected_district: GetVacantByDistrictAndPost_Interface | null;
  loading_schools: boolean;
  selected_qualification: string;
  search: string;
  step: number;
  search_district: string;
  search_school: string;
  selected_schools: GetFreeVacanciesByDistrictAndUser_Interface[];
  reasons: ReasonsTransfers[];
  selected_reasons: ReasonsTransfers[];
  loading_reasons: boolean;
  candidate_comment: string;
  transfer_success: boolean;
  loading_submit_transfer: boolean;
}

class _RequestTransferUI extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading_schools: false,
      loading_district: true,
      districts: [],
      schools: [],
      selected_district: null,
      error: {
        target: null,
        msg: "",
      },
      selected_qualification: "",
      search: "",
      step: 1,
      search_district: "",
      search_school: "",
      selected_schools: [],
      reasons: [],
      selected_reasons: [],
      loading_reasons: false,
      candidate_comment: "",
      transfer_success: false,
      loading_submit_transfer: false,
    };
  }

  componentDidMount = () => {
    if (!this.props.auth.position?.position_code)
      return this.setState({
        error: {
          msg: "You have no position assigned to you.",
          target: "main-error",
        },
      });

    if (!this.props.auth.position?.qualification_id)
      return this.setState({
        error: {
          msg: "You have no qualification assigned to you.",
          target: "main-error",
        },
      });

    FC_GetVacantByDistrictAndPost(
      this.props.auth.position.qualification_id,
      this.props.auth.position.position_code,
      (status, data, msg) => {
        this.setState({
          loading_district: false,
          districts: data === null ? [] : data,
          error: !status
            ? {
                target: "main-error",
                msg: msg,
              }
            : {
                target: null,
                msg: "",
              },
        });
      }
    );
  };

  selectDistrict = (district: GetVacantByDistrictAndPost_Interface | null) => {
    if (
      district !== null &&
      this.props.auth.user &&
      this.props.auth.position?.position_code &&
      this.props.auth.position?.qualification_id
    ) {
      if (district.total_vacant >= 1) {
        this.setState({
          selected_district: district,
          step: 2,
          loading_schools: true,
        });

        FC_GetFreeVacanciesByDistrictAndUser(
          district.district_code,
          this.props.auth.position?.position_code,
          this.props.auth.position?.qualification_id,
          (status, data, error) => {
            this.setState({
              loading_schools: false,
              schools: data,
              error:
                status === true
                  ? {
                      target: null,
                      msg: "",
                    }
                  : {
                      target: "main-error",
                      msg: error,
                    },
            });
          }
        );
      } else {
        window.alert(`No vacant found in ${district.district_name} district.`);
      }
    } else {
      window.alert(`Please verify your profile`);
    }
  };

  selectSchool = (
    school: GetFreeVacanciesByDistrictAndUser_Interface,
    add: boolean = true
  ) => {
    if (this.state.selected_schools.length >= 3 && add) return;

    if (add) {
      this.setState({
        selected_schools: [...this.state.selected_schools, school],
      });
    } else {
      this.setState({
        selected_schools: this.state.selected_schools.filter(
          (sc) => sc.school_code !== school.school_code
        ),
      });
    }
  };

  loadReasons = () => {
    if (this.state.reasons.length <= 0) {
      this.setState({
        loading_reasons: true,
      });
      FC_GetTransfersReasons((status, data, error) => {
        this.setState({
          loading_reasons: false,
          reasons: data,
          error:
            status === false
              ? { msg: "", target: null }
              : { target: "error", msg: error },
        });
      });
    }
  };

  submitTransferRequest = () => {
    // if (!this.props.basic.academic_calender) {
    //   return this.setState({
    //     error: {
    //       target: "transfer-error",
    //       msg: "Academic calendar is not valid",
    //     },
    //   });
    // }

    if (!this.props.auth) {
      return this.setState({
        error: {
          target: "transfer-error",
          msg: "Authentication info are not completed",
        },
      });
    }

    if (!this.props.auth.school) {
      return this.setState({
        error: {
          target: "transfer-error",
          msg: "Check your school information.",
        },
      });
    }

    if (!this.state.selected_district) {
      return this.setState({
        error: {
          target: "transfer-error",
          msg: "The district assigned to you is not valid",
        },
      });
    }

    if (!this.props.auth.user) {
      return this.setState({
        error: {
          target: "transfer-error",
          msg: "Incomplete profile info",
        },
      });
    }

    if (
      !this.props.basic.academic_calender ||
      !this.props.auth ||
      !this.props.auth.school ||
      !this.state.selected_district ||
      !this.props.auth.user
    ) {
      return this.setState({
        error: {
          target: "transfer-error",
          msg: "Incomplete profile info",
        },
      });
    }

    let district_current = this.props.auth.school.school_code.substring(0, 2);

    let data: SendTransferRequestInterface = {
      // academic_year_id: this.props.basic.academic_calender.academic_year_id,
      academic_year_id: "1",
      district_from_id: district_current,
      district_to_id: this.state.selected_district.district_code,
      requested_school_id: this.state.selected_schools.map(
        (itm) => itm.school_code
      ),
      school_from_id: this.props.auth.school.school_code,
      teacher_comment: this.state.candidate_comment,
      teacher_reason_id: [],
      teacher_supporting_document: [],
      transfer_type:
        district_current !== this.state.selected_district.district_code
          ? "External"
          : "Internal",
    };

    this.setState({
      loading_submit_transfer: true,
      error: {
        target: null,
        msg: "",
      },
    });
    FC_SendTransferRequest(
      data,
      this.props.auth.user?.staff_code,
      (status, data, msg) => {
        console.log({ res: data });
        if (status === true) {
          this.setState({
            loading_submit_transfer: false,
            transfer_success: true,
          });
        } else {
          console.log({ msg: msg });

          this.setState({
            loading_submit_transfer: false,
            error: {
              target: "transfer-error",
              msg: msg,
            },
          });
          this.props.reloadTransfers();
        }
      }
    );
  };

  render() {
    if (this.props.auth.is_transfer_open === false) {
      return <TransferClosed />;
    }

    if (
      this.props.auth.user?.resident_surpoting_document === null ||
      this.props.auth.user?.resident_surpoting_document === "" ||
      !this.props.auth.user?.resident_district_id
    )
      return (
        <DashboardContainer>
          <div className="text-center py-4 bg-red-100 bg-opacity-60 mb-4 text-red-800">
            <h1 className="text-2xl font-bold">
              Your document of residence is required
            </h1>
            <p className="text-gray-600">
              Please add the document in order to continue, the document can be
              found on <b>Irembo</b>
            </p>
          </div>

          <UserDocuments
            auth={this.props.auth}
            FC_UpdateUserDocuments={this.props.FC_UpdateUserDocuments}
          />
        </DashboardContainer>
      );

    if (this.state.error.target === "main-error") {
      return (
        <Alert theme="danger" fixed>
          {this.state.error.msg}
        </Alert>
      );
    }

    let DistrictResults: GetVacantByDistrictAndPost_Interface[] = search(
      this.state.districts,
      this.state.search_district,
      { district_code: true, district_name: true }
    );

    let SchoolsResults: GetFreeVacanciesByDistrictAndUser_Interface[] = search(
      this.state.schools,
      this.state.search_school,
      { school_code: true, school_name: true, sector_name: true }
    );

    if (this.state.transfer_success === true)
      return (
        <DashboardContainer className="animate__animated animate__zoomIn animate__faster">
          <div className="py-4 text-center">
            <MdCheckCircle size={90} className="mx-auto text-green-500" />

            <h3 className="font-bold text-lg">
              {this.props.auth.user?.full_name}
            </h3>

            <p className="text-gray-500 pb-4">
              Your transfer request has been submitted successfully.{" "}
            </p>

            <Button
              theme="accent-light"
              size="md"
              onClick={() => this.props.close()}
            >
              View transfer request
            </Button>
          </div>
        </DashboardContainer>
      );

    // teaching_level_id(pin):"1"
    // education_certificate_id(pin):"1"

    return (
      <>
        {this.state.error.target === "main-success" && (
          <Alert theme="success" fixed>
            {this.state.error.msg}
          </Alert>
        )}

        <>
          <div className="sticky top-14 z-50 ">
            <DashboardContainer>
              <div className="flex items-center">
                <div className="flex-1">
                  <Heading
                    size="lg"
                    className=" py-2 lg:pb-3 lg:pt-2 font-light text-left text-gray-500"
                  >
                    Apply for transfer{" "}
                    {this.state.selected_district && (
                      <span>
                        {" "}
                        in{" "}
                        <b className="text-primary-800">
                          {this.state.selected_district.district_name} district
                        </b>
                      </span>
                    )}
                  </Heading>
                  <p className="-mt-3 pb-2 text-gray-400">
                    Position name:{" "}
                    <b className="text-primary-800">
                      {this.props.auth.position?.position_code}{" "}
                      {this.props.auth.position?.position_name}
                    </b>
                  </p>
                  <p className="-mt-3 pb-2 text-gray-400">
                    Qualification:{" "}
                    <b className="text-primary-800">
                      {this.props.auth.position?.qualification_name}
                    </b>
                  </p>
                </div>

                {this.state.loading_submit_transfer === false && (
                  <Button
                    size="md"
                    theme="danger-light"
                    onClick={() =>
                      window.confirm(
                        "Are you sure you want to stop the request process \n All your previous choices will be lost"
                      )
                        ? this.props.onClick()
                        : null
                    }
                  >
                    Close
                  </Button>
                )}
              </div>

              <div className="pt-2 grid  grid-cols-3 border p-2 rounded-md">
                {[
                  {
                    step: 1,
                    title: "Choose a district",
                    description:
                      "Select a district where you want to be transferred",
                  },
                  {
                    step: 2,
                    title: "Choose school",
                    description: "You can select up to 3 schools",
                  },
                  {
                    step: 3,
                    title: "Complete transfer",
                    description: "Complete the information",
                  },
                ].map((itm) => (
                  <CardTransferRequest
                    onClick={() => {
                      if (this.state.loading_submit_transfer === true) return;

                      if (itm.step === 1) {
                        this.setState({
                          step: 1,
                          schools: [],
                          loading_schools: false,
                          selected_schools: [],
                        });
                      }

                      // go back to step 2
                      if (itm.step === 2) {
                        if (this.state.step === 3)
                          this.setState({
                            step: 2,
                          });
                      }
                    }}
                    key={itm.step}
                    active={itm.step === this.state.step}
                    description={itm.description}
                    number={itm.step}
                    theme={
                      itm.step === this.state.step
                        ? "yellow"
                        : this.state.step > itm.step
                        ? "green"
                        : "gray"
                    }
                    title={itm.title}
                    priority={itm.step <= this.state.step}
                  />
                ))}
              </div>
            </DashboardContainer>
          </div>

          {this.state.step === 1 && (
            <DashboardContainer className="animate__animated animate__zoomIn animate__faster">
              <div>
                <div className="p-2">
                  <TextInput
                    error=""
                    label=""
                    name=""
                    onChange={(e) =>
                      this.setState({ search_district: e.target.value })
                    }
                    placeholder="Search district by name"
                    value={this.state.search_district}
                  />
                </div>

                {this.state.loading_district === true ? (
                  <div className="py-8 text-center text-8xl text-accent-900">
                    <Loading className="mx-auto" />
                  </div>
                ) : this.state.districts.length >= 1 ? (
                  <>
                    {DistrictResults.length <= 0 ? (
                      <div className="py-4 text-center">
                        <h2 className="text-lg">No results found!</h2>
                        <p className="text-gray-500">
                          Please use a correct district name
                        </p>
                      </div>
                    ) : (
                      <div className="grid lg:grid-cols-2 gap-2 ">
                        {DistrictResults.map((dist) => (
                          <div
                            onClick={this.selectDistrict.bind(this, dist)}
                            key={dist.district_code}
                            className="p-2 flex items-center cursor-pointer rounded border  px-3 hover:text-yellow-600 hover:border-yellow-600 hover:bg-yellow-50 bg-white text-lg"
                          >
                            <h4 className="flex-1 text-lg">
                              {" "}
                              <b>{dist.district_name}</b> district
                            </h4>

                            <span className="bg-yellow-100 text-yellow-700 font-bold py-0.5 inline-block text-sm px-1.5 rounded-full">
                              {dist.total_vacant}
                            </span>
                            {dist.total_vacant >= 1 && (
                              <button className="">
                                <MdOutlineArrowForward />
                              </button>
                            )}
                          </div>
                        ))}
                      </div>
                    )}
                  </>
                ) : (
                  <div className="py-4 text-center">
                    <h2 className="text-8xl text-gray-400 text-center">
                      <ImSad className="inline" />
                    </h2>
                    <h2 className="text-center p-4 text-gray-400 font-bold text-2xl">
                      <p>No vacant found on</p>
                    </h2>{" "}
                    <p className="-mt-3 pb-2 text-gray-400 text-lg">
                      <b className="text-black">
                        {this.props.auth.position?.position_code} -{" "}
                        {this.props.auth.position?.position_name} -{" "}
                        {this.props.auth.position?.qualification_name}
                      </b>
                    </p>
                  </div>
                )}
              </div>
            </DashboardContainer>
          )}

          {this.state.step === 2 && (
            <>
              {this.state.loading_schools === true ? (
                <div className="py-4 text-center">
                  <MdSearch
                    size={"160px"}
                    className="text-gray-600 mx-auto animate__animated animate__swing animate__slow animate__infinite"
                  />
                  <h2 className="text-3xl text-gray-600 pb-2 pt-4">
                    Looking for the vacancies
                  </h2>
                  <p className="text-gray-500">Please wait</p>
                </div>
              ) : (
                <DashboardContainer className="animate__animated animate__zoomIn animate__faster">
                  <div>
                    <div className="py-0.5 my-2 px-2 flex items-center bg-gray-100 rounded">
                      <section>
                        <Button
                          theme="default"
                          className="flex items-center text-blue-600"
                          onClick={() =>
                            this.setState({
                              step: 1,
                              schools: [],
                              loading_schools: false,
                              selected_schools: [],
                            })
                          }
                        >
                          <MdKeyboardBackspace size={30} /> Back
                        </Button>
                      </section>
                      <section className="flex-1">
                        <TextInput
                          error=""
                          label=""
                          name=""
                          onChange={(e) =>
                            this.setState({ search_school: e.target.value })
                          }
                          placeholder="Search schools by name"
                          value={this.state.search_school}
                        />
                      </section>
                    </div>

                    {this.state.schools.length >= 1 ? (
                      <>
                        {SchoolsResults.length <= 0 ? (
                          <div className="py-4 text-center">
                            <h2 className="text-lg">No results found!</h2>
                            <p className="tet-gray-500">
                              Please use a correct school name
                            </p>
                          </div>
                        ) : (
                          <div className="grid lg:grid-cols-2 gap-2 pb-20">
                            {SchoolsResults.map((dist) => {
                              let selectedSchool =
                                this.state.selected_schools.find(
                                  (itm) => itm.school_code === dist.school_code
                                );

                              if (
                                dist.school_code ===
                                this.props.auth.school?.school_code
                              )
                                return null;

                              return (
                                <div
                                  onClick={this.selectSchool.bind(
                                    this,
                                    dist,
                                    selectedSchool === undefined ? true : false
                                  )}
                                  key={dist.school_code}
                                  className={`pr-2 pl-1 py-1 flex cursor-pointer rounded border  px-3 ${
                                    selectedSchool === undefined
                                      ? "hover:text-yellow-600 hover:border-yellow-600 hover:bg-yellow-50"
                                      : "hover:text-green-600 border-green-600 bg-green-100"
                                  }  bg-white text-lg`}
                                >
                                  <button className="pr-2 text-3xl">
                                    {selectedSchool === undefined ? (
                                      <MdCheckBoxOutlineBlank
                                        className={`${
                                          this.state.selected_schools.length >=
                                          3
                                            ? "text-gray-400"
                                            : "text-gray-800"
                                        }`}
                                      />
                                    ) : (
                                      <MdCheckBox className="text-green-500" />
                                    )}
                                  </button>

                                  <section className="flex-1">
                                    <h4 className="">
                                      <span>{dist.school_code}</span>{" "}
                                      <b>{dist.school_name}</b>
                                    </h4>
                                    <p className="text-sm text-gray-600">
                                      Sector: <b>{dist.sector_name}</b>
                                    </p>
                                  </section>
                                  <section>
                                    <p className="text-xs text-gray-400">
                                      Vacant{`(s)`}
                                    </p>
                                    <h4 className={` font-bold text-right   `}>
                                      {dist.vacant_posts}
                                    </h4>
                                  </section>
                                </div>
                              );
                            })}
                          </div>
                        )}
                      </>
                    ) : (
                      <div className="py-6 text-center">
                        <ImSad
                          size={"120px"}
                          className="text-gray-600 mx-auto animate__animated animate__zoomIn animate__slow "
                        />
                        <h2 className="text-3xl text-gray-600 pb-2 pt-4">
                          Sorry, there is no vacancies for you in{" "}
                          <b>{this.state.selected_district?.district_name}</b>
                        </h2>
                        <p className="text-gray-500">
                          Please look for another district, <br /> or try again
                          next time
                        </p>
                      </div>
                    )}
                  </div>
                </DashboardContainer>
              )}

              {this.state.selected_schools.length >= 1 && (
                <div
                  className={`p-2 bottom-0 items-center right-0 left-0 lg:pl-72 fixed bg-white border-t flex`}
                >
                  <section className="flex-1">
                    <label className="block px-0.5 m-0 text-sm">
                      Selected schools:{" "}
                      <b>{this.state.selected_schools.length}</b>
                    </label>
                    <section className="pt-0.5">
                      {this.state.selected_schools.map((itm) => (
                        <section
                          key={itm.school_code}
                          className="inline-block m-0.5 p-0.5 px-1 rounded-md bg-yellow-100 font-bold text-yellow-600 text-sm"
                        >
                          {itm.school_name}
                        </section>
                      ))}
                    </section>
                  </section>
                  <div className="">
                    <Button
                      size="lg"
                      onClick={() => {
                        this.setState({
                          step: 3,
                        });
                        // this.loadReasons();
                      }}
                    >
                      Continue
                    </Button>
                  </div>
                </div>
              )}
            </>
          )}

          {this.state.step === 3 && (
            <DashboardContainer className="animate__animated animate__zoomIn animate__faster">
              {this.state.error.target === "transfer-error" && (
                <Alert theme="danger">{this.state.error.msg}</Alert>
              )}
              <div className="p-1">
                <UserDocuments
                  auth={this.props.auth}
                  FC_UpdateUserDocuments={this.props.FC_UpdateUserDocuments}
                />

                <div className="pt-3">
                  <h4>Add reason</h4>
                  <TextArea
                    onChange={(e) =>
                      this.setState({ candidate_comment: e.target.value })
                    }
                    value={this.state.candidate_comment}
                    error={""}
                    label={""}
                    placeholder={"Explain your reasons"}
                    name={""}
                    disabled={this.state.loading_submit_transfer}
                  />
                </div>

                <div className="text-right">
                  <Button
                    size="lg"
                    onClick={this.submitTransferRequest}
                    loading={this.state.loading_submit_transfer}
                  >
                    Submit transfer request
                  </Button>
                </div>
              </div>
            </DashboardContainer>
          )}
        </>
      </>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
  placement,
}: StoreState): {
  auth: Auth;
  basic: Basic;
  placement: Placement;
} => {
  return {
    auth: auth,
    basic: basic,
    placement: placement,
  };
};

export const RequestTransferUI = connect(mapStateToProps, {
  FC_UpdateUserDocuments,
})(_RequestTransferUI);
