import React, { FC, ReactElement } from "react";

type CardTransferRequestProps = {
  number: number;
  title: string;
  description: string;
  theme: "blue" | "green" | "gray" | "yellow";
  active: boolean;
  priority: boolean;
  onClick: () => void;
};

const CardTransferRequest: FC<CardTransferRequestProps> = ({
  number,
  title,
  description,
  theme,
  active,
  priority,
  onClick,
}): ReactElement => {
  /* function body */
  return (
    <div
      onClick={onClick}
      className={`p-2 rounded-t-md cursor-pointer border-b-4 flex flex-col text-center lg:text-left lg:flex-row items-center border-${theme}-500 ${
        active === true ? `bg-${theme}-100` : ""
      } ${priority === false ? "opacity-60" : ""}`}
    >
      <section
        className={`p-2 lg:p-3 rounded-full border-4 ${
          theme === "yellow" ? `bg-${theme}-500` : `bg-${theme}-500`
        } 0 ${
          theme === "yellow" ? `border-${theme}-600` : `border-${theme}-200`
        }  ${
          theme === "yellow" ? "text-black" : "text-white"
        } w-9 h-9 lg:w-14 lg:h-14 flex items-center text-center`}
      >
        <h3 className="text-center w-full text-xl lg:text-3xl font-bold ">
          {number}
        </h3>
      </section>
      <section className="pl-2">
        <h3 className="lg:text-xl text-lg font-bold">{title}</h3>
        <p className="text-gray-400 text-sm">{description}</p>
      </section>
    </div>
  );
};

export default CardTransferRequest;
