import React from "react";
import {
  MdCheckCircle,
  MdInfoOutline,
  MdOutlineRemoveCircle,
} from "react-icons/md";
import { connect } from "react-redux";
import {
  Auth,
  Basic,
  FC_GetTransferForUser,
  FC_RemoveTransferByUser,
  FC_UpdateTransferTeacherDecision,
  GetTransferForUser__Interface,
  TransferDecisionStatus,
} from "../../actions";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import DateEligibleErrorDisplay from "../../components/DateEligibleErrorDisplay/DateEligibleErrorDisplay";
// import NonValidatedErrorDisplay from "../../components/DateEligibleErrorDisplay/NonValidatedErrorDisplay";
import Heading from "../../components/Heading/Heading";
import Loading from "../../components/Loading/Loading";
import { StoreState } from "../../reducers";
import { checkAccess, UsersAccess } from "../../utils/access";
// import { validateAuthUser } from "../../utils/validatedStaff";
import { RequestTransferUI } from "./RequestTransfeForm";
import TransferClosed from "./TransferClosed";
import TransferItem from "./TransferItem";

// props for the component
interface AppProps {
  auth: Auth;
  basic: Basic;
}

interface AppState {
  loading: boolean;
  add_transfer: boolean;
  transfers: GetTransferForUser__Interface[];
  current_transfer: GetTransferForUser__Interface | null;
  error: {
    target:
      | "main-error"
      | "main-success"
      | "teacher-decision-success"
      | "teacher-decision-error"
      | null;
    msg: string;
  };
  deleting_transfer_id: string;
  has_permutation_request: boolean;
  loading_user_decision: boolean;
}

class _TransfersList extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      add_transfer: false,
      transfers: [],
      current_transfer: null,
      error: {
        target: null,
        msg: "",
      },
      deleting_transfer_id: "",
      has_permutation_request: false,
      loading_user_decision: false,
    };
  }

  componentDidMount() {
    this.startComponent(true);
  }

  removeTransfer = (id: string) => {
    let academic_year_id = this.props.basic.academic_calender?.academic_year_id;
    if (!academic_year_id)
      return this.setState({
        error: {
          msg: "No academic year found",
          target: "main-error",
        },
      });

    if (
      !window.confirm("Are you sure you want to remove your transfer request?")
    )
      return;
    this.setState({
      deleting_transfer_id: id,
    });
    FC_RemoveTransferByUser(id, academic_year_id, (status, msg) => {
      if (status === true) {
        this.setState({
          deleting_transfer_id: "",
          current_transfer:
            this.state.current_transfer !== null &&
            this.state.current_transfer.teacher_transfer_id === id
              ? null
              : this.state.current_transfer,
          transfers: this.state.transfers.filter(
            (el) => el.teacher_transfer_id !== id
          ),
          error: {
            target: "main-success",
            msg: msg,
          },
        });
      } else {
        this.setState({
          deleting_transfer_id: "",
          error: {
            target: "main-error",
            msg: msg,
          },
        });
      }
    });
  };

  startComponent(mount: boolean = false) {
    if (mount === true)
      this.setState({
        loading: true,
        // add_transfer: false,
      });
    FC_GetTransferForUser((status, data, msg) => {
      if (status === true && data !== null) {
        this.setState({
          loading: false,
          has_permutation_request: data.has_permutation_request,
          transfers: data.transferWithNoCurrentAcademic
            ? data.transferWithNoCurrentAcademic
            : [],
          current_transfer: data.transferWithCurrentAcademic,
        });
      } else {
        if (mount === true) {
          this.setState({
            transfers: [],
            loading: false,
            error: {
              target: "main-error",
              msg: msg,
            },
          });
        }
      }
    });
  }

  openForm = (status: boolean) => this.setState({ add_transfer: status });

  UpdateTransferTeacherDecision = (value: TransferDecisionStatus) => {
    if (this.state.current_transfer === null || value === null)
      return this.setState({
        error: {
          target: "teacher-decision-error",
          msg: "No selected transfer found!",
        },
      });

    if (
      window.confirm(
        `Are you sure you want to ${value} your transfer?. this process is irreversible.`
      )
    )
      if (this.props.auth.user) {
        this.setState({
          loading_user_decision: true,
        });
        FC_UpdateTransferTeacherDecision(
          {
            teacher_decision: value,
            teacher_transfer_id:
              this.state.current_transfer.teacher_transfer_id + "",
          },
          (status, data, msg) => {
            if (status === true) {
              this.setState({
                loading_user_decision: false,
                current_transfer:
                  this.state.current_transfer !== null
                    ? {
                        ...this.state.current_transfer,
                        teacher_decision: value,
                      }
                    : null,
                error: {
                  target: "teacher-decision-success",
                  msg: `Your transfer has been ${value} successfully.`,
                },
              });

              if (value === "REJECTED") {
                window.alert(
                  "Your changes has been applied, Please Reload your browser in order to see the changes"
                );
                window.location.reload();
              }
            } else {
              this.setState({
                loading_user_decision: false,
                error: {
                  target: "teacher-decision-error",
                  msg: `Error: ${msg}.`,
                },
              });
            }
          }
        );
      }
  };

  render() {
    if (this.state.has_permutation_request === true)
      return (
        <DashboardContainer className="max-w-screen-md mx-auto py-4 text-center animate__animated animate__zoomIn animate__faster">
          <h2 className=" text-6xl text-center text-red-400 pb-4">
            <MdInfoOutline className="inline-block" />
          </h2>
          <h4 className="text-xl font-bold">
            It looks like you have already requested for a permutation, <br />
            You can't Request a transfer
          </h4>
          <p className="text-gray-600 pt-2">
            If you want to request for a transfer, <br /> you will need to
            remove your permutation.
          </p>
        </DashboardContainer>
      );

    if (!this.props.auth.has_one_year)
      return (
        <DateEligibleErrorDisplay
          title="You are not qualified to request a transfer"
          name="transfer"
        />
      );

    // let ProfileValidation = validateAuthUser(this.props.auth);
    // if (
    //   ProfileValidation.errors.length >= 1 &&
    //   ProfileValidation.validated === false
    // )
    //   return (
    //     <NonValidatedErrorDisplay
    //       title="transfer"
    //       errors={ProfileValidation.errors}
    //     />
    //   );

    // if (this.props.auth.role?.role_id !== "1")
    //   return (
    //     <DashboardContainer className="animate__animated animate__zoomIn animate__faster max-w-lg mx-auto">
    //       <div className="py-8 text-center">
    //         <div className="text-8xl">
    //           <MdWarningAmber className="text-yellow-300 mx-auto" />
    //         </div>
    //         <h3 className="text-2xl ">You can't request a transfer</h3>
    //         <h4 className="pt-2 text-gray-600">
    //           The transfers is open for teachers only for teachers
    //         </h4>
    //       </div>
    //     </DashboardContainer>
    //   );

    if (this.state.add_transfer === true) {
      if (this.props.auth.is_transfer_open === false) {
        return <TransferClosed />;
      } else {
        if (
          checkAccess(
            this.props.auth.role?.access,
            UsersAccess.TRANSFER_FORM_OPEN
          )
        ) {
          return (
            <RequestTransferUI
              onClick={this.openForm.bind(this, false)}
              reloadTransfers={this.startComponent}
              close={() => {
                this.setState({
                  add_transfer: false,
                });
                this.startComponent();
              }}
            />
          );
        } else {
          return (
            <div className="p-4 text-xl text-center text-gray-400">
              Transfer is closed
            </div>
          );
        }
      }
    }

    return (
      <>
        <div className="bg-white">
          <DashboardContainer transparent className="">
            <div className="flex items-center">
              <section className="flex-1 text-gray-600">
                <Heading>My Transfers</Heading>
                {this.state.loading === false && (
                  <p>
                    Total transfers:{" "}
                    {this.state.current_transfer === null
                      ? this.state.transfers.length
                      : this.state.transfers.length + 1}
                  </p>
                )}
              </section>
              <section>
                {this.state.current_transfer !== null &&
                  this.state.current_transfer.requested_status ===
                    "PENDING" && (
                    <p className="text-sm text-yellow-700 ">
                      If you want to make changes on your request, <br /> delete
                      the current request and request a new one.
                    </p>
                  )}
              </section>
            </div>
          </DashboardContainer>
        </div>

        <DashboardContainer transparent className="">
          {this.state.error.target === "main-error" && (
            <Alert
              theme="danger"
              fixed
              onClick={() =>
                this.setState({ error: { msg: "", target: null } })
              }
            >
              {this.state.error.msg}
            </Alert>
          )}

          {this.state.error.target === "main-success" && (
            <Alert
              theme="success"
              fixed
              onClick={() =>
                this.setState({ error: { msg: "", target: null } })
              }
            >
              {this.state.error.msg}
            </Alert>
          )}

          <div className="py-2">
            {this.state.loading === false && (
              <>
                {this.state.current_transfer === null ||
                !this.state.current_transfer.teacher_transfer_id ? (
                  <>
                    {checkAccess(
                      this.props.auth.role?.access,
                      UsersAccess.TRANSFER_FORM_OPEN
                    ) ? (
                      <div className="text-center py-8">
                        <h2 className="text-2xl font-bold ">
                          You haven't yet requested for a transfer?
                        </h2>
                        <p className="text-gray-500 pb-2">
                          Send your transfer application if you want to be
                          transferred
                        </p>

                        <Button onClick={this.openForm.bind(this, true)}>
                          Request new transfer
                        </Button>
                      </div>
                    ) : (
                      <TransferClosed />
                    )}
                  </>
                ) : (
                  <>
                    {this.state.error.target === "teacher-decision-error" && (
                      <Alert theme="danger">{this.state.error.msg}</Alert>
                    )}
                    {this.state.error.target === "teacher-decision-success" && (
                      <Alert theme="success">{this.state.error.msg}</Alert>
                    )}
                    <TransferItem
                      transf={this.state.current_transfer}
                      expand
                      remove_loading={
                        this.state.current_transfer.teacher_transfer_id ===
                        this.state.deleting_transfer_id
                          ? true
                          : false
                      }
                      removeTransfer={
                        this.props.auth.is_transfer_open === true
                          ? this.removeTransfer
                          : undefined
                      }
                    />

                    {this.state.current_transfer !== null &&
                      this.state.current_transfer.approved_school_id !== null &&
                      this.state.current_transfer.requested_status ===
                        "APPROVED" && (
                        <div className="p-2 bg-white -mt-4 border rounded flex">
                          <div className="flex-1"></div>
                          {this.state.current_transfer.teacher_decision !==
                          null ? (
                            <>
                              <div className="text-right p-3 flex items-center gap-2">
                                <p className="text-gray-700">
                                  Teacher's transfer decision
                                </p>
                                {this.state.current_transfer
                                  .teacher_decision === "ACCEPTED" ? (
                                  <span className="p-2 text-green-600 bg-green-100 rounded font-bold px-2 py-1">
                                    ACCEPTED
                                  </span>
                                ) : (
                                  <span className="p-2 text-red-600 bg-red-100 rounded font-bold px-2 py-1">
                                    {
                                      this.state.current_transfer
                                        .teacher_decision
                                    }
                                  </span>
                                )}
                              </div>
                            </>
                          ) : (
                            <>
                              <Button
                                className="mr-2"
                                theme="danger-light"
                                onClick={this.UpdateTransferTeacherDecision.bind(
                                  this,
                                  "REJECTED"
                                )}
                                loading={this.state.loading_user_decision}
                              >
                                <section className="flex items-center">
                                  <section className="text-4xl pr-3">
                                    <MdOutlineRemoveCircle />
                                  </section>
                                  <section className="text-left flex-1">
                                    <h4>Reject Transfer</h4>
                                    <p className="font-light text-sm">
                                      Hagarika taransiferi
                                    </p>
                                  </section>
                                </section>
                              </Button>

                              <Button
                                className="mr-2"
                                theme="success"
                                onClick={this.UpdateTransferTeacherDecision.bind(
                                  this,
                                  "ACCEPTED"
                                )}
                                loading={this.state.loading_user_decision}
                              >
                                <section className="flex items-center">
                                  <section className="text-4xl pr-3">
                                    <MdCheckCircle />
                                  </section>
                                  <section className="text-left flex-1">
                                    <h4>Accept Transfer</h4>
                                    <p className="font-light text-sm">
                                      Emeza taransiferi
                                    </p>
                                  </section>
                                </section>
                              </Button>
                            </>
                          )}
                        </div>
                      )}
                  </>
                )}
              </>
            )}
          </div>
          <div
            className={`pt-5 ${
              this.state.current_transfer !== null &&
              this.state.current_transfer?.teacher_transfer_id !== null &&
              this.state.current_transfer?.teacher_transfer_id !== undefined &&
              this.state.current_transfer?.teacher_transfer_id?.length >= 1
                ? "opacity-40"
                : ""
            }`}
          >
            {this.state.loading === true ? (
              <div className="py-8 text-center text-6xl">
                <Loading className="mx-auto" />
              </div>
            ) : this.state.transfers.length <= 0 ? (
              <div className="text-3xl py-8 text-center text-gray-500">
                {/* No transfers found! */}
              </div>
            ) : (
              <>
                <h2 className="text-2xl text-gray-700 font-bold pb-2">
                  Old transfers
                </h2>
                {this.state.transfers.map((transf, i) => (
                  <TransferItem transf={transf} key={i} />
                ))}
              </>
            )}
          </div>
        </DashboardContainer>
      </>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
}: StoreState): {
  auth: Auth;
  basic: Basic;
} => {
  return { auth: auth, basic: basic };
};

export const TransfersList = connect(mapStateToProps, {})(_TransfersList);
